.content-box {
  display: inline;
}
.flex-container {
  display: flex;
}

.flex-child {
  flex: 1;
}

.flex-child:first-child {
  margin-right: 20px;
}

@media screen and (max-width: 500px) {
  .box {
    display: flex;
    margin: 0;
    padding: 0;
    flex-direction: column-reverse;
  }
}
